import React from 'react';
import { Card } from 'react-bootstrap';
const PortfolioItem = ( { imageLink , index } ) => {
    return (
        <div key={ index } className="col-md-4 col-sm-6">
            <Card>
                <div className="front">
                <Card.Img variant="top" src={ process.env.PUBLIC_URL + imageLink.url } alt={ imageLink.altImage } />
                </div>
                <div className="back">
                    <Card.Link target='_blank' rel="noopener noreferrer" href={ imageLink.webLink } className="btn btn-primary btn-custom">Visit Site</Card.Link>
                </div>
            </Card>
        </div>
    );
}

export default PortfolioItem;