import React, { Fragment, useState } from 'react';
import SkillItem from './SkillItem';
import Tada from 'react-reveal/Tada';
const Skill = () => {
    const [skillState] = useState(
        [
            { name:'HTML',percent: 90},
            { name:'CSS',percent: 90},
            { name:'LESS',percent: 80 },
            { name:'BOOTSTRAP',percent: 70 },
            { name:'JAVASCRIPT',percent: 50 },
            { name:'JQUERY',percent: 60 },
            { name:'PHP',percent: 40 },
            { name:'React',percent: 30 },
            { name:'Node.JS',percent: 10 },
            { name:'Express',percent: 10 },
            { name:'Wordpress',percent: 70 }
        ]
    );
    return (
        <Fragment>
        <section className="skill-section section" >
            <div className="container">
                <div className="row">
                    <div className="col-md-12 text-center mb-2">
                      <Tada delay={3500} duration={2500}>
                        <h2> My Skills</h2>
                     </Tada>

                    </div>
                </div>
                <div className="row">
                 <div className="skill-list">
                 <ul>
                    {
                    skillState.map(( skill, index ) => (
                        <SkillItem  key={ index } skill ={ skill }/>
                    ))
                    }
                </ul>
                </div>

            </div>
            </div>
        </section>
        </Fragment>
    );
}

export default Skill;
