import React from 'react'
import { ProgressBar, Badge } from 'react-bootstrap';
const SkillItem = ({ skill, index }) => {

    return (
        <li key={ index } className="skill-box">
        <h4 className="mb-1"> { skill.name } </h4>
        {
            skill.percent <=30 ?  <div><Badge className="ml-0 mt-0">Basic</Badge><ProgressBar animated now={ skill.percent } variant="success" /> </div>
            : skill.percent >=70 ? <div> <Badge className="ml-0 mt-0" variant="danger">Advance</Badge><ProgressBar animated now={ skill.percent } variant="danger" /></div>
            : <div><Badge className="ml-0 mt-0">Intermidiate</Badge ><ProgressBar animated now={ skill.percent }variant="warning"  /></div>
        }
       </li>
    );
}

export default SkillItem;